.twitter {
  display: flex;
  list-style: none;
  margin: 12px 24px;
  border: 1px solid #eeeeee;
  color: #757575;
  border-radius: 8px;
  padding: 12px 24px;
  box-shadow: 5px 10px 20px -16px black;
}
.twitter li {
  display: flex;
  cursor: pointer;
  margin-right: 8px;
}
.twitter li:hover {
  color: #55acee;
}
.twitter li span {
  margin-left: 4px;
}
.twitter .twitter__icon {
  margin-left: auto;
  margin-right: auto;
  font-size: 1.3em;
  color: #55acee;
}
.twitter .twitter_tweet {
  margin-right: 0;
}
.twitter .twitter_tweet button {
  background: #55acee;
  border-radius: 8px;
  font-size: 0.8em;
  color: white;
}



